<template>
    <div class="forgot">
        <a-breadcrumb class="bread">
            <a-breadcrumb-item>
                <a href="/customerService/pc">{{ $t("language.service") }}</a>
            </a-breadcrumb-item>
            <a-breadcrumb-item class="text">{{ breadcrumb }}</a-breadcrumb-item>
        </a-breadcrumb>

        <div class="content">
            <steps
                v-if="pageIm != 5"
                :index="index"
                :listName="listName"
            ></steps>
            <div class="fromBox">
                <template v-if="index == 1">
                    <div>
                        <a-row class="rowItem">
                            <a-col :span="8">
                                <p class="lable">
                                    {{ $t("language.memberaccount") }}
                                </p>
                            </a-col>
                            <a-col :span="12">
                                <a-input
                                    v-model="formState.username"
                                    :placeholder="$t('language.account')"
                                />
                            </a-col>
                        </a-row>
                        <a-row class="rowItem">
                            <a-col :span="8">
                                <p class="lable">
                                    {{ $t("language.nameRel") }}
                                </p>
                            </a-col>
                            <a-col :span="12">
                                <a-input
                                    v-model="formState.name"
                                    :placeholder="$t('language.relname')"
                                />
                            </a-col>
                        </a-row>
                        <a-row class="rowItem">
                            <a-col :span="8">
                                <p class="lable">
                                    {{ $t("language.verification") }}
                                </p>
                            </a-col>
                            <a-col :span="12">
                                <div class="flexCode">
                                    <a-input
                                        v-model="formState.code"
                                        type="number"
                                        :placeholder="
                                            $t('language.verification1')
                                        "
                                    />
                                    <div>
                                        <img
                                            :src="captchaImg"
                                            class="codeImg"
                                            @click="getCaptcha"
                                        />
                                    </div>
                                </div>
                            </a-col>
                        </a-row>

                        <a-row class="rowItem">
                            <a-col :span="8"> </a-col>
                            <a-col :span="12" class="btnCol">
                                <a-button
                                    type="primary"
                                    class="btnNext"
                                    @click="validateMember"
                                >
                                    {{ btnText }}
                                </a-button>
                            </a-col>
                        </a-row>
                    </div>
                </template>
                <template v-if="index == 2">
                    <div>
                        <a-row class="rowItem">
                            <a-col :span="8">
                                <p class="lable">
                                    {{ $t("language.phonecode") }}
                                </p>
                            </a-col>
                            <a-col :span="12">
                                <a-input
                                    v-model="formState.mphone"
                                    disabled
                                    :placeholder="$t('language.talPhone')"
                                />
                            </a-col>
                        </a-row>
                        <a-row class="rowItem">
                            <a-col :span="8">
                                <p class="lable">
                                    {{ $t("language.codeText") }}
                                </p>
                            </a-col>
                            <a-col :span="12">
                                <div class="flexBox">
                                    <a-input
                                        v-model="formState.pcode"
                                        :placeholder="
                                            $t('language.verification1')
                                        "
                                    />
                                    <a-button
                                        type="primary"
                                        class="btnCode"
                                        :disabled="disabledBut"
                                        @click="setPhoneCode"
                                    >
                                        {{ codeButtext }}
                                    </a-button>
                                </div>
                            </a-col>
                        </a-row>

                        <a-row class="rowItem btnTop">
                            <a-col :span="8"> </a-col>
                            <a-col :span="12" class="btnCol">
                                <a-button
                                    type="primary"
                                    class="btnNext"
                                    @click="onNext"
                                >
                                    {{ $t("language.submit") }}
                                </a-button>
                            </a-col>
                        </a-row>
                    </div>
                </template>
                <template v-if="index == 3 && pageIm == 1">
                    <div>
                        <a-row class="rowItem">
                            <a-col :span="8">
                                <p class="lable">{{ $t("language.newP") }}:</p>
                            </a-col>
                            <a-col :span="12">
                                <a-input-password
                                    v-model="formState.password"
                                    :placeholder="$t('language.inputP')"
                                />
                            </a-col>
                        </a-row>
                        <a-row class="rowItem">
                            <a-col :span="8">
                                <p class="lable">
                                    {{ $t("language.confirmpassword") }}:
                                </p>
                            </a-col>
                            <a-col :span="12">
                                <a-input-password
                                    v-model="formState.password1"
                                    :placeholder="$t('language.inputs')"
                                />
                            </a-col>
                        </a-row>
                        <a-row class="rowItem btnTop">
                            <a-col :span="8"> </a-col>
                            <a-col :span="12" class="btnCol">
                                <a-button
                                    type="primary"
                                    class="btnNext"
                                    @click="resetPassword(1)"
                                >
                                    {{ $t("language.submit") }}
                                </a-button>
                            </a-col>
                        </a-row>
                    </div>
                </template>
                <template v-if="index == 3 && pageIm == 3">
                    <div>
                        <a-row class="rowItem">
                            <a-col :span="8">
                                <p class="lable">
                                    {{ $t("language.bankname") }}:
                                </p>
                            </a-col>
                            <a-col :span="12">
                                <a-select
                                    ref="select"
                                    @change="memberChange"
                                    class="select"
                                    :placeholder="$t('language.banknames')"
                                >
                                    <a-select-option
                                        v-for="(item, i) of memberBanks"
                                        :key="i"
                                        :value="item.id"
                                    >
                                        {{ item.name }}(
                                        {{ item.number | bankcode }})
                                    </a-select-option>
                                </a-select>
                            </a-col>
                        </a-row>
                        <a-row class="rowItem">
                            <a-col :span="8">
                                <p class="lable">
                                    {{ $t("language.bankcode") }}:
                                </p>
                            </a-col>
                            <a-col :span="12">
                                <a-input
                                    v-model="formState.number"
                                    :placeholder="$t('language.bankcodein')"
                                />
                            </a-col>
                        </a-row>
                        <a-row class="rowItem btnTop">
                            <a-col :span="8"> </a-col>
                            <a-col :span="12" class="btnCol">
                                <a-button
                                    type="primary"
                                    class="btnNext"
                                    @click="updateBank(2)"
                                >
                                    {{ $t("language.submit") }}
                                </a-button>
                            </a-col>
                        </a-row>
                    </div>
                </template>
                <template v-if="index == 3 && pageIm == 2">
                    <div>
                        <a-row class="rowItem">
                            <a-col :span="8">
                                <p class="lable">
                                    {{ $t("language.bankcode") }}:
                                </p>
                            </a-col>
                            <a-col :span="12">
                                <a-select
                                    ref="select"
                                    @change="memberChange"
                                    class="select"
                                    :placeholder="$t('language.bankcodes')"
                                >
                                    <a-select-option
                                        v-for="(item, i) of memberBanks"
                                        :key="i"
                                        :value="item.id"
                                    >
                                        {{ item.name }}({{
                                            item.number | bankcode
                                        }})
                                    </a-select-option>
                                </a-select>
                            </a-col>
                        </a-row>
                        <a-row class="rowItem">
                            <a-col :span="8">
                                <p class="lable">
                                    {{ $t("language.bankname") }}:
                                </p>
                            </a-col>
                            <a-col :span="12">
                                <a-select
                                    ref="select"
                                    @change="handleChange"
                                    class="select"
                                    :placeholder="$t('language.banknames')"
                                >
                                    <a-select-option
                                        v-for="(item, i) of bankLists"
                                        :key="i"
                                        :value="item.name"
                                    >
                                        {{ item.name }}
                                    </a-select-option>
                                </a-select>
                            </a-col>
                        </a-row>

                        <a-row class="rowItem btnTop">
                            <a-col :span="8"> </a-col>
                            <a-col :span="12" class="btnCol">
                                <a-button
                                    type="primary"
                                    class="btnNext"
                                    @click="updateBank(1)"
                                >
                                    {{ $t("language.submit") }}
                                </a-button>
                            </a-col>
                        </a-row>
                    </div>
                </template>
                <template v-if="index == 3 && pageIm == 4">
                    <div>
                        <a-row class="rowItem">
                            <a-col :span="8">
                                <p class="lable">{{ $t("language.newP") }}:</p>
                            </a-col>
                            <a-col :span="12">
                                <a-input-password
                                    v-model="formState.password"
                                    :placeholder="$t('language.inputP')"
                                />
                            </a-col>
                        </a-row>
                        <a-row class="rowItem">
                            <a-col :span="8">
                                <p class="lable">
                                    {{ $t("language.confirmpassword") }}:
                                </p>
                            </a-col>
                            <a-col :span="12">
                                <a-input-password
                                    v-model="formState.password1"
                                    :placeholder="$t('language.inputs')"
                                />
                            </a-col>
                        </a-row>

                        <a-row class="rowItem btnTop">
                            <a-col :span="8"> </a-col>
                            <a-col :span="12" class="btnCol">
                                <a-button
                                    type="primary"
                                    class="btnNext"
                                    @click="resetPassword(2)"
                                >
                                    {{ $t("language.submit") }}
                                </a-button>
                            </a-col>
                        </a-row>
                    </div>
                </template>
            </div>
        </div>
    </div>
</template>

<script>
import Steps from "../components/Steps";
import MyAes from "../utils/myAes";
export default {
    components: {
        steps: Steps,
    },
    filters: {
        bankcode: function (value) {
            return value.substr(value.length - 4);
        },
    },
    data() {
        return {
            formState: {
                mphone: "",
            },
            index: 1,
            pageIm: 0, //1忘记密码  2修改银行名称  3修改银行卡号 4忘记取款密码
            breadcrumb: "",
            listName: ["资料核实", ""],
            intervalID: "",
            codeButtext: "获取验证码",
            disabledBut: false,
            bankLists: [], //银行列表
            memberBanks: [], //银行卡列表
            bankData: "",
            bankName: "",
            sign: "",
            btnText: "下一步",
            subimgBtn: true,
            captchaImg: "",
            captchaKey: "",
        };
    },
    created() {
        this.formState.username = this.$config.username;
        this.pageIm = this.$route.params.id;
        if (this.pageIm == 1) {
            this.breadcrumb = this.$t("language.password");
            this.listName = [
                this.$t("language.textVer"),
                this.$t("language.verPhone"),
                this.$t("language.newPwass"),
            ];
        } else if (this.pageIm == 2) {
            this.breadcrumb = this.$t("language.modifybank");
            this.listName = [
                this.$t("language.textVer"),
                this.$t("language.verPhone"),
                this.$t("language.bankSe"),
            ];
        } else if (this.pageIm == 3) {
            this.breadcrumb = this.$t("language.modifybankcode");
            this.listName = [
                this.$t("language.textVer"),
                this.$t("language.verPhone"),
                this.$t("language.bankSe"),
            ];
        } else if (this.pageIm == 4) {
            this.breadcrumb = this.$t("language.withdrawal");
            this.listName = [
                this.$t("language.textVer"),
                this.$t("language.verPhone"),
                this.$t("language.newPwass"),
            ];
        } else if (this.pageIm == 5) {
            this.breadcrumb = this.$t("language.unfreeze");
            this.btnText = this.$t("language.defrosting");
        }
        this.getCaptcha();
    },
    methods: {
        //获取图形验证码
        getCaptcha() {
            this.$http.get(this.$api.getCaptcha).then((res) => {
                console.log("图片", res);
                if (res.code == 0) {
                    this.captchaKey = res.data.captchaKey;
                    this.captchaImg = res.data.captcha_image_content;
                }
            });
        },
        //选择自己的银行卡卡
        memberChange(e) {
            console.log(e);
            this.bankData = this.memberBanks.filter((item) => {
                return item.id == e;
            });
        },
        //银行卡号选择
        handleChange(e) {
            console.log(e);
            this.bankName = e;
        },
        //获取银行卡列表信息
        getbankList() {
            let data = {
                sign: this.sign,
            };
            this.$http.post(this.$api.getbankList, data).then((res) => {
                if (res.code == 0) {
                    console.log(res);
                    this.bankLists = res.data.bankLists; //银行列表
                    this.memberBanks = res.data.memberBanks; //银行卡列表
                }
            });
        },
        //更新银行卡信息
        updateBank(i) {
            if (!this.bankData) {
                return;
            }
            let params = {
                id: this.bankData[0].id,
                sign: this.sign,
                type: i,
            };
            //2修改银行名称  3修改银行卡号
            if (this.pageIm == 3) {
                if (
                    !(
                        this.formState.number.length > 12 &&
                        this.formState.number.length <= 19
                    )
                ) {
                    this.$message.warning(this.$t("language.texterr"));
                    return;
                }
                params.number = this.formState.number;
                params.name = this.bankData[0].name;
            } else {
                params.name = this.bankName;
                params.number = this.bankData[0].number;
            }
            if (!this.subimgBtn) {
                return;
            }
            this.subimgBtn = !this.subimgBtn;
            this.$http.post(this.$api.updateBank, params).then((res) => {
                this.subimgBtn = !this.subimgBtn;
                if (res.code == 0) {
                    console.log(res);
                    this.$message.success(this.$t("language.successfully"));
                    this.$router.go(-1);
                }
            });
        },
        onNext() {
            if (this.pageIm == 2 || this.pageIm == 3) {
                this.getbankList();
            }
            if (this.pageIm == 5) {
                console.log("验证陈宫");
                let params = {
                    phone: this.formState.phone,
                    smsCode: this.formState.pcode,
                    sign: this.sign,
                };
                this.$http.post(this.$api.accountThaw, params).then((res) => {
                    console.log("图片", res);
                    if (res.code == 0) {
                        this.$message.success(this.$t("language.textSu"));
                        this.$router.go(-1);
                    }
                });
            } else {
                let params = {
                    phone: this.formState.phone,
                    smsCode: this.formState.pcode,
                };
                this.$http
                    .post(this.$api.validateSmsCode, params)
                    .then((res) => {
                        if (res.code == 0) {
                            console.log(res);
                            this.index = 3;
                        }
                    });
            }
        },
        //重置密码
        resetPassword(i) {
            if (!this.subimgBtn) {
                return;
            }
            if (!this.formState.password) {
                this.$message.warning(this.$t("language.inputP"));
                return;
            }
            if (this.formState.password != this.formState.password1) {
                this.$message.warning(this.$t("language.inconsistent"));
                return;
            }
            if (i == 1) {
                if (!/^[a-zA-Z0-9]{6,15}$/.test(this.formState.password)) {
                    this.$message.warning(this.$t("language.text3"));
                    return;
                }
                var aseKey = "pigxpigxpigxpigx"; //秘钥
                // Encrypt 加密
                this.formState.password = MyAes.encrypt(
                    this.formState.password,
                    aseKey
                );
            }
            if (i == 2) {
                if (this.formState.password.length != 6) {
                    this.$message.warning(this.$t("language.text2"));
                    return;
                }

                if (!/^[0-9]*$/.test(this.formState.password)) {
                    this.$message.warning(this.$t("language.text2"));
                    return;
                }
                var aseKeys = "pigxpigxpigxpigx"; //秘钥
                // Encrypt 加密
                this.formState.password = MyAes.encrypt(
                    this.formState.password,
                    aseKeys
                );
            }
            let params = {
                password: this.formState.password,
                resetType: i, //	重置类型 1：重置默认密码，2：重置提款密码
                sign: this.sign,
            };
            this.subimgBtn = !this.subimgBtn;
            this.$http.post(this.$api.resetPassword, params).then((res) => {
                this.subimgBtn = !this.subimgBtn;
                if (res.code == 0) {
                    console.log(res);
                    this.$message.success(this.$t("language.successfully"));
                    this.$router.go(-1);
                }
            });
        },
        //获取验证签名
        validateMember() {
            if (!this.formState.username) {
                this.$message.warning(this.$t("language.account"));
                return;
            }
            if (!this.formState.name) {
                this.$message.warning(this.$t("language.relname"));
                return;
            }

            let params = {
                name: this.formState.username,
                realName: this.formState.name,
                captchaCode: this.formState.code,
                captchaKey: this.captchaKey,
            };

            if (this.$config.clientItem == "aygj") {
                if (!this.$config.username) {
                    params.name = params.name + "ay";
                } else {
                    var disLength = params.name.length;
                    var shortName = params.name.substring(
                        disLength - 2,
                        disLength
                    );
                    if (shortName != "ay") {
                        params.name = params.name + "ay";
                    }
                }
            }
            this.$http.post(this.$api.validateMember, params).then((res) => {
                if (res.code == 0) {
                    console.log(res);
                    this.formState.phone = res.data.phone;
                    this.index = 2;
                    var phone = res.data.phone;
                    var reg = /(\d{3})\d{4}(\d{4})/; //正则表达式
                    this.formState.mphone = phone.replace(reg, "$1****$2");
                    this.sign = res.data.sign;
                } else {
                    this.getCaptcha();
                }
            });
        },
        //获取验证码
        setPhoneCode() {
            if (!/^1[3456789]\d{9}$/.test(this.formState.phone)) {
                this.$message.error(this.$t("language.Malformed"));
                return;
            }
            this.$http
                .get(
                    `${this.$api.setPhoneCode}${this.formState.phone}?functionId=6`
                )
                .then((res) => {
                    console.log(res);
                    if (res.code == 0) {
                        this.disabledBut = true;
                        this.$message.success(
                            this.$t("language.codesuccessfully")
                        );
                        var time = 60;
                        this.intervalID = setInterval(() => {
                            time--;
                            this.codeButtext = time + "S";
                            if (time == 0) {
                                this.disabledBut = false;
                                clearInterval(this.intervalID);
                                this.codeButtext = this.$t("language.getCode");
                                time = 60;
                            }
                        }, 1000);
                    }
                });
        },
        onSubmit() {
            this.index = 2;
            console.log(this.formState);
            if (this.pageIm == 2 || this.pageIm == 3) {
                this.getbankList();
            }
        },
    },
};
</script>

<style lang="scss" >
.forgot {
    margin: 15px 18% 0;

    .content {
        margin: 20px 15% 0;
        text-align: center;
        .fromBox {
            background: #fff;
            padding: 70px 30px;
            margin-top: 27px;
            .lable {
                margin: 0;
                text-align: right;
                line-height: 44px;
                padding-right: 10px;
                color: #333;
                font-size: 16px;
            }
        }
        .select {
            width: 100%;
            height: 44px;
        }
        .rowItem {
            margin-bottom: 20px;
            .btnCol {
                text-align: left;
                .btnNext {
                    background: var(--themeColor);
                    border: none;
                    padding-left: 35px;
                    padding-right: 35px;
                }
            }
            .flexBox {
                display: flex;
                .btnCode {
                    margin-left: 10px;
                    background: var(--themeColor);
                    border: none;
                    color: #fff;
                }
            }
            .flexCode {
                display: flex;
                .codeImg {
                    height: 44px;
                    margin-left: 10px;
                }
            }
        }
        .btnTop {
            margin-top: 70px;
        }
    }
    .ant-select-selection--single {
        height: 44px;
    }
    .ant-select-selection-selected-value {
        line-height: 44px !important;
    }
    .ant-select-selection__rendered {
        line-height: 44px !important;
    }
}
</style>